<script>
import * as util from "@/common/util.js";

export default {
  components: {},
  data() {
    return {
      index: 0,
      total: 0,
      active: null,
      title: "input",
      defaultValue: "", // 默认值
      dataList: [],
      types: "image",
      startData: "",
      brandIndex: 0,
      modelIndex: 0,
      companyIndex: 0,
      inputValue: "",
      showPassword: true,
      isTime: false,
      isNumber: true,
      isTrailerNum: true,
      isAmbulanceNum: true,
      checkbox6: [0],
      baseFormData: {
        sex: 0,
      },
      filesArray: [],
      historySteps: [],
      isEscort: [],

      isDrive: [],
      isTrafficPolice: [],
      isFirePerson: [],

      otherItems: [],
      params: {
        status: 1,
        createUserName: null,
        phone: null,
        startAddress: null,
        endAddress: null,
        appointDate: null,
        description: null,
        fileId: [],

        accidentCarNum: 1,
        injuredNum: 0,
        isDrive: 0,
        ambulanceNum: 0,
        trailerNum: 0,
        isTrafficPolice: 0,
        isFirePerson: 0,
        isEscort: 0,
        injuredType: null,
        hospitalId: null,
      },
    };
  },
  onLoad(options) {
    uni.setStorageSync("historyDetailId", options.dataId);
    this.getHistoryDetail();
    this.getHistorySteps();
  },
  mounted() {
    if (uni.getStorageSync("address") == null) {
      this.params.startAddress = uni.getStorageSync("startAddress");
    } else {
      this.params.startAddress = uni.getStorageSync("address");
    }
    this.params.endAddress = uni.getStorageSync("endAddress");
  },
  methods: {
    handleEmergencyCourse() {
      uni.navigateTo({
        url: "/pages/emergencyCourse/emergencyCourse",
      });
    },
    getHistoryDetail() {
      console.log(uni.getStorageSync("historyDetailId"));
      let id = uni.getStorageSync("historyDetailId");
      this.$http.historyDetail({ id: id }).then((res) => {
        const { status, data } = res;
        if (status == 200) {
          console.log(data);
          let injuredTypeName = "";
          this.params = data;
          this.params.accidentCarNum = data.accidentCarNum + "辆";
          this.params.ambulanceNum = data.ambulanceNum + "辆";
          this.params.trailerNum = data.trailerNum + "辆";
          this.params.injuredNum = data.injuredNum + "人";
          this.params.isDrive = data.isDrive == 0 ? "不需要" : "需要";
          this.params.isFirePerson = data.isFirePerson == 0 ? "不需要" : "需要";
          this.params.isTrafficPolice =
            data.isTrafficPolice == 0 ? "不需要" : "需要";
          this.params.isEscort = data.isEscort == 0 ? "不需要" : "需要";
          switch (data.injuredType) {
            case 1:
              injuredTypeName = "轻伤";
              break;
            case 2:
              injuredTypeName = "重伤";
              break;
          }
          let otherItems = data.otherNeed.split(",");
          let newOtherItems = [];
          console.log(otherItems);
          otherItems.forEach((_item) => {
            console.log(_item);
            switch (_item) {
              case "1":
                newOtherItems.push("换胎");
                break;
              case "2":
                newOtherItems.push("搭电");
                break;
              case "3":
                newOtherItems.push("加油");
                break;
              case "4":
                newOtherItems.push("洗车");
                break;
            }
          });
          this.otherItems = newOtherItems;
          console.log(newOtherItems);

          this.params.injuredType = injuredTypeName;
          let faultFilesArrayList = [];
          let filesArray = data.file.split(",");

          if (filesArray) {
            filesArray.forEach((item) => {
              let FaultFilesStr =
                "http://192.168.100.177:31141/api/sys/attachment/v1/getFileStream?id=" +
                item;
              faultFilesArrayList.push(FaultFilesStr);
            });
            this.filesArray = faultFilesArrayList;
          }
        } else {
          uni.showToast({
            title: res.message,
            icon: "none",
            duration: 2000,
          });
          // uni.navigateTo({
          //   url: "/pages/emergencyCourse/emergencyCourseDetail?id=" + res.data.id,
          // });
        }
      });
    },

    getHistorySteps() {
      console.log(uni.getStorageSync("historyDetailId"));
      let id = uni.getStorageSync("historyDetailId");
      this.$http.historySteps({ rescueId: id }).then((res) => {
        const { status, data } = res;
        if (status == 200) {
          console.log("data =>", data);
          let historyItems = data.records;
          this.historySteps = data.records;
          this.active = this.historySteps.length - 1;
          console.log(this.historySteps.length - 1);
          historyItems.forEach((item, index) => {
            this.historySteps[index].title = item.statusName;
            this.historySteps[index].desc = item.createTime;
          });
        } else {
          uni.showToast({
            title: res.message,
            icon: "none",
            duration: 2000,
          });
          // uni.navigateTo({
          //   url: "/pages/emergencyCourse/emergencyCourseDetail?id=" + res.data.id,
          // });
        }
      });
    },
  },
};
</script>

